.blinkingblue{
  animation:blinkingBlue 1.2s infinite;
  font-size: 130%;
}

.blinkinggreen{
  animation:blinkingGreen 1.2s infinite;
  font-size: 130%;
}

@keyframes blinkingBlue{
  0%{     color: rgb(7, 242, 241);    }
  49%{    color:rgb(7, 242, 241); }
  60%{    color: transparent; }
  99%{    color:transparent;  }
  100%{   color: rgb(7, 242, 241);    }
}

@keyframes blinkingGreen{
  0%{     color: green;    }
  49%{    color:green; }
  60%{    color: transparent; }
  99%{    color:transparent;  }
  100%{   color: green;    }
}

